import React, { Component } from 'react';
import HomepageSlider from './homepageSlider/Slider';
import { Col } from 'reactstrap';
import './style.css';
import TopFourContent from './topFourContent/TopFourContent';
import HomepageTopContent from './homepageTopContent/homepageTopContent';
import {Helmet} from "react-helmet";

class Homepage extends Component {
    render() {
        return (
            <div>
              <Helmet>
                  <title>صفحه ی اصلی</title>
                  <meta name="description" content="Helmet application" />
              </Helmet>
              <Col xs="12" className="px-0 homepageContent">
                <HomepageTopContent isEn={this.props.isEn} />
                <HomepageSlider />
                <TopFourContent isEn={this.props.isEn} />
              </Col>
            </div>
          );
    }
}

export default Homepage;