import React, { useState } from 'react';
import { Col, Button, Collapse, Navbar, NavbarToggler, Nav, NavItem } from 'reactstrap';
import SearBar from './searchBar/SearchBar';
import { Link } from 'react-router-dom';
import Logo from './logo/Logo'
import './style.css';
import data from '../../fixedComponents/header/topMenu/data.json'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faChevronDown } from '@fortawesome/free-solid-svg-icons';
import $ from 'jquery';

const HomepageTopContent = (props) => {
    const [collapsed, setCollapsed] = useState(true);

    const toggleNavbar = () => setCollapsed(!collapsed);

    const scrollBody = () => {
        $('html, body').animate({
            scrollTop: window.innerHeight
        },1000);
    }

    if(window.innerWidth > 768){
        return(
            <Col xs="12" className="px-0 homepageTopContent">
                <Navbar color="faded" dark className="pt-2">
                    <NavbarToggler onClick={toggleNavbar} />
                    <Collapse isOpen={!collapsed} navbar>
                    <Nav navbar className={(props.isEn ? "text-left" : "text-right")}>
                        <NavItem ><Link to={data[0].data[0].link}>{(props.isEn ? data[0].data[0].En_name : data[0].data[0].name)}</Link></NavItem>
                        <NavItem ><a href={data[0].data[1].href}>{(props.isEn ? data[0].data[1].En_name : data[0].data[1].name)}</a></NavItem>
                        <NavItem ><a href={data[0].data[2].href}>{(props.isEn ? data[0].data[2].En_name : data[0].data[2].name)}</a></NavItem>
                    </Nav>
                    </Collapse>
                </Navbar>
                <Logo />
                <SearBar isEn={props.isEn} />
                <div
                style={{
                    color: '#fff',
                    top: '70%',
                    position: 'absolute',
                    textAlign: 'center',
                    left: '35%',
                    fontSize: '22px'
                    }}
                >
                    <p>به نام خدا</p>
                    <p>امیرحسین سروری - دانشجوی ترم 6 مهندسی کامپیوتر دانشگاه تهران</p>
                    <p>Front-End Developer</p>
                </div>
                <Button onClick={scrollBody} className="scrollDown"><FontAwesomeIcon icon={faChevronDown} /></Button>
            </Col>
        );
    }
    else {
        return(null);
    }
}

export default HomepageTopContent;