import React, { Component } from "react";
import './style.css';
import data from '../../../fixedComponents/header/topNavigation/searchBar/data.json';
import { Col } from "reactstrap";
import { Link } from 'react-router-dom';

class SearchBar extends Component{
    constructor() {
        super();
        this.state = {
            lists: []
        }
    }

    componentDidMount() {
        document.getElementsByTagName('body')[0].onclick = function(event) {
            if(document.getElementById('searchResult') && event.target !== document.getElementById('searchInput')){
                document.getElementById('searchResult').style.display = 'none';
            }
        }
    }

    // doesExistLink(id) {
    //     let doesExist = false;
    //     this.state.lists.map((list) => {
    //         if(list.key === id){
    //             doesExist = true;
    //         }
    //         return true;
    //     });
    //     return doesExist;
    // }

    clearSearchField() {
        document.getElementById('searchInput').value = '';
    }

    sendResults(event) {
        const inputText = event.target.value;
        if(inputText !== ''){
            document.getElementById('searchResult').style.display = 'block';
            let list = [];
            data[0].data.map((data) => {
                if(((this.props.isEn ? data.En_name.includes(inputText): data.value.includes(inputText)))){
                    if(data.link)
                        list.push(<li key={data.id} className="p-1 allTransitions"><Link to={data.link} onClick={this.clearSearchField.bind(this)} className="allTransitions">{data.value}</Link></li>);
                    else
                        list.push(<li key={data.id} className="p-1 allTransitions"><a href={data.href} onClick={this.clearSearchField.bind(this)} className="allTransitions">{data.value}</a></li>);
                }
                return true;
            });
            if(!list.length) {
                list.push(<li key="0" className="p-1 allTransitions"><span>{(this.props.isEn ? 'No result' : 'نتیجه ای یافت نشد')}</span></li>);
            }
            this.setState({
                lists: list
            });
        }
        else{
            document.getElementById('searchResult').style.display = 'none';
        }
    }

    render() {
        return(
            <div className="searchContent text-right search px-0 d-flex align-items-center mb-md-2 mb-lg-0" dir="rtl">
                <Col xs="12" className="px-0 d-flex align-items-center searchText">
                    <input type="text" name="search" id="searchInput" placeholder={(this.props.isEn ? "Type what you are looking for" : "کلمه ی مورد نظر را وارد کنید")} className={"px-2" + (this.props.isEn ? ' text-left' : '')} onChange={this.sendResults.bind(this)} dir={(this.props.isEn ? 'ltr' : 'rtl')} />
                    <div className={"px-2 searchIcon allTransitions d-flex align-items-center justify-content-center" + (this.props.isEn ? ' right' : '')} >{<img src="Images/SearchLogo/searchLogo.svg" alt="search" width="20" height="20" />}</div>
                    <ul id="searchResult" className={(this.props.isEn ? ' text-left' : '')}>
                        {this.state.lists}
                    </ul>
                </Col>
            </div>
        );
    }
}

export default SearchBar;