import React, { Component } from 'react';
import './App.css';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Layout from './components/layout/Layout';
import pages from './pages.js';

class App extends Component {
  constructor () {
    super();
    this.state = {
      isEn: false
    }
  }

  render() {
    return (
      <Router>
        <Layout>
          <Switch>
            {
              pages.map((page) => {
                return(
                  <Route key={page.id} path={page.path} render={() => <page.component isEn={this.state.isEn} location={page.path} />}/>
                )
              })
            }
          </Switch>
        </Layout>
      </Router>
    );
  }
}

export default App;