import React from "react";
import './style.css';
import { Col } from "reactstrap";


const Logo = (props) => {
    return(
        <Col xs="3" className="d-inline-block pr-0 logoResponsive text-right">
            <img src="/Images/Logo/Logo.png" alt="articles" width="50" />
        </Col>
    );
}

export default Logo;