import React from "react";
import SearchBar from './searchBar/SearchBar';
// import LoginOrSignup from './loginOrSignup/LoginOrSignup';
import Logo from './logo/Logo';
import { Col, Container } from "reactstrap";
import './style.css';


const TopNavigation = () => {
    if(window.innerWidth < 769){
        return(
            <Col xs="12" className="topNav px-0">
                <Container>
                    <Col xs="12" className="d-flex align-items-center py-2 d-lg-none px-0" dir="rtl">
                        {/* <LoginOrSignup parentSize="9" size="8" offset="2" /> */}
                        <Logo />
                        <SearchBar />
                    </Col>
                    <Col xs="12" className="text-center infoDiv mt-2">
                        <span>برای استفاده از بخش پرسش و پاسخ در ابتدا وارد سایت شوید</span>
                    </Col>
                </Container>
            </Col>
        );
    }
    else {
        return(null);
    }
}

export default TopNavigation;