import React from 'react';
import { Container, Col } from 'reactstrap';
import './style.css';

const Footer = (props) => {
    return(
        <Col xs="12" className="blueBackground text-center whiteColor footer py-2">
            <Container>
                <span>
                    {
                        (
                            props.isEn
                            ?
                                "All rights reserved by Amirhossein Sarvari"
                            :
                                "کلیه ی حقوق این سایت متعلق به امیرحسین سروری میباشد"
                        )
                    }
                </span>
            </Container>
        </Col>
    );
}

export default Footer;