import React from 'react';
import { Container, Col, Row, Button } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faChevronUp } from '@fortawesome/free-solid-svg-icons';
import Fade from 'react-reveal/Fade';
import data from './data.json';
import './style.css'
import $ from 'jquery';
import UserServices from '../../../services/UserServices';

const TopFourContent = (props) => {

    const scrollBody = () => {
        $('html, body').animate({
            scrollTop: 0
        },1000);
    }

    const userServices = new UserServices();

    return(
        <Row dir="rtl" className="backgroundTopFourContent align-items-center d-flex justify-content-center" id="summary">
            <Container className="px-md-0 topFourContent align-items-center d-flex justify-content-center">
                <Col xs="12" className="px-0 mt-4 align-items-center d-flex justify-content-center flex-column">
                <Col xs="12" className="px-0 mt-4 align-items-center d-flex justify-content-center flex-column" style={{color: '#000', fontSize: '20px', backgroundColor: 'rgba(256,256,256,0.8)', marginBottom: '10px', maxWidth: '555px', borderRadius: '4px'}}>
                    <p>درس Foundations of Information Technology</p>
                    <p>در این درس به مباحث مربوط به IT پرداخته شده است.</p>
                    <p style={{textAlign: "right", padding: "10px"}}>مباحثی از جمله تکنولوژی ، فناوری ، فناوری اطلاعات ، مفاعهیم مربوط به تکنولوژی ، مدل گراندد تئوری ، سیستم های اطلاعاتی و ...</p>
                    <p style={{textAlign: "right", padding: "10px"}}>پروژه های این درس شامل بررسی یک استارت آپ و محاسبه ی ارزش آن در بازار و مقایسه با ارزش اصلی (قیمت گذاری استارت آپ) ، و پروژه ی اصلی شامل کار تحقیقاتی و مصاحبه و کدگذاری و همچنین بدست آوردن مدل گراندد تئوری یک استارت آپ بود.</p>
                </Col>
                    {
                        data.map((data) => {
                            return(
                                <Col key={data.id} xs="12" md="12" className="text-center mb-4 align-items-center d-flex justify-content-center">
                                    <Fade up>
                                        <Col xs="12" md="12" className="px-0 allTransitions text-center align-items-center d-flex justify-content-center">
                                            <Col xs="12" md="6" className={"px-0 allTransitions"} style={{cursor: 'pointer'}}>
                                                <a onClick={(event) => userServices.downloadPdf(event)} className="d-block" style={{color: "#fff"}}>
                                                    <img src={data.src} alt={data.name} height="280"></img>
                                                    <div className="py-1 py-md-2 blueBackground">{(props.isEn ? data.En_name : data.name)}</div>
                                                </a>
                                            </Col>
                                        </Col>
                                    </Fade>
                                </Col>
                            )
                        })
                    }
                    <Button onClick={scrollBody} className="scrollTop d-none d-lg-inline-block"><FontAwesomeIcon icon={faChevronUp} /></Button>
                </Col>
            </Container>
        </Row>
    );
}

export default TopFourContent;