import React from 'react';
import images from './data.json';
import SliderItems from './SliderItems';
import './style.css';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import "react-responsive-carousel/lib/styles/carousel.css";
import { Carousel } from 'react-responsive-carousel';
import { Col, Button } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faChevronDown } from '@fortawesome/free-solid-svg-icons';
import $ from 'jquery';


const HomepageSlider = () => {
    const slideImages = images;

    const scrollBody = () => {
        $('html, body').animate({
            scrollTop: 2 * window.innerHeight
        },1000);
    }

    return (
        <Col xs="12" className="px-3 px-md-0 carouselBox" id="slider">
            <Carousel useKeyboardArrows={true} showArrows={true} infiniteLoop={true} showIndicators={false} showThumbs={false} stopOnHover={false} autoPlay={true} showStatus={false} swipeable={true} emulateTouch={true}>
                {slideImages.map((image) => {
                    return(<SliderItems key={image.id} src={image.src} alt={image.alt} />)
                })}
            </Carousel>
            <Button onClick={scrollBody} className="scrollDown d-none d-lg-inline-block"><FontAwesomeIcon icon={faChevronDown} /></Button>
        </Col>
    )
}

export default HomepageSlider;